import React,{useState} from 'react';
import PicoGraphQL from 'picosanity-graphql';
import moment from 'moment';

const client = new PicoGraphQL({
    projectId: '1vvrsqbl',
    dataset: 'production'
  })
  
export default function Page(props){
    const [content, setcontent] = useState([])
    const [loaded, setloaded] = useState(false)
    async function initialLoad(){
        const content = await client.fetch( `
        query{
          allPost(sort:{publishedAt:DESC},limit: 4, offset: 0) {
            _id
            publishedAt
            mainImage {
              asset {
                url
              }
            }
            title
            slug{
              current
            }
          }
        }`);
        setcontent(content.data.allPost);
        setloaded(true);
      };
    initialLoad();
    return (
        <div>
            
            <div className="header half blacked total-center">

            <div className="middle-heading fadeIn">
                blog
            </div>
            </div>
            <div className="header top-center">
                <div className="w-100 flex flex-row row">
                {content && content.map((post)=>
                <div className="col col-12 col-lg-4 col-md-6 col-sm-12 p-1" key={post._id}>
                <div>
                    <div  className="blogpost">
                    <img
                    alt="blogpost"
                    src={post.mainImage.asset.url} />
                    <div className="text-muted">{moment(post.publishedAt).format("Do MMMM YYYY")}</div>
                    <div className="blog-post-title">{post.title}</div>
                    
                    </div>
                </div>
                </div>
                
                )}
                </div>
            </div>
        </div>
    )
}